  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 0.5rem;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.sxa-card .card-wrapper:hover {
  transform: translateY(-5px);
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  border-color: var(--color-primary);
}
.sxa-card .card-image {
  position: relative;
  overflow: hidden;
}
.sxa-card .card-image .card-image-placeholder {
  width: 100%;
  transition: all 0.5s ease-in-out;
}
.sxa-card .card-image .card-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 6px 12px;
  background: var(--color-accent);
  color: white;
  font-size: 12px;
  font-weight: 600;
  border-radius: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.sxa-card .card-image:hover .card-image-placeholder {
  transform: scale(1.05);
}
.sxa-card .card-content {
  padding: 25px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}
.sxa-card .card-header {
  margin-bottom: 15px;
}
.sxa-card .card-header .card-title {
  margin: 0 0 8px 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1.4;
}
.sxa-card .card-header .card-meta {
  font-size: 13px;
  color: var(--text-muted);
}
.sxa-card .card-description {
  margin: 0 0 20px 0;
  font-size: 15px;
  line-height: 1.8;
  color: var(--text-secondary);
  flex-grow: 1;
}
.sxa-card .card-footer {
  margin-top: auto;
}
.sxa-card .card-footer .card-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--color-primary);
  text-decoration: none;
  font-weight: 600;
  font-size: 15px;
  transition: all 0.15s ease-in-out;
}
.sxa-card .card-footer .card-link svg {
  transition: all 0.15s ease-in-out;
}
.sxa-card .card-footer .card-link:hover {
  color: var(--color-primary-dark);
}
.sxa-card .card-footer .card-link:hover svg {
  transform: translateX(5px);
}
.sxa-card[data-variant=horizontal] .card-wrapper {
  flex-direction: row;
}
@media (max-width: 768px) {
  .sxa-card[data-variant=horizontal] .card-wrapper {
    flex-direction: column;
  }
}
.sxa-card[data-variant=horizontal] .card-image {
  flex-shrink: 0;
  width: 300px;
}
@media (max-width: 768px) {
  .sxa-card[data-variant=horizontal] .card-image {
    width: 100%;
  }
}
.sxa-card[data-variant=horizontal] .card-image .card-image-placeholder {
  height: 100%;
  min-height: 250px;
}
.sxa-card[data-variant=image-top] .card-content {
  padding: 20px;
}
.sxa-card[data-variant=image-top] .card-title {
  font-size: 1.25rem;
}
.sxa-card[data-variant=icon-based] .card-image {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px;
  background: var(--bg-surface);
}
.sxa-card[data-variant=icon-based] .card-image .card-icon {
  font-size: 64px;
}
.sxa-card[data-variant=icon-based] .card-image .card-image-placeholder {
  display: none;
}
.sxa-card[data-variant=icon-based] .card-content {
  text-align: center;
}
.sxa-card[data-variant=icon-based] .card-footer {
  display: flex;
  justify-content: center;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 30px;
}
@media (max-width: 576px) {
  .card-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
